.dish {
    position: relative;
    margin-top: 157px;
    // z-index: -1;

    &--active {
        z-index: 100;
        .dish__box {
                &:after{
                    opacity: 1;
                    z-index: 1;
                    height: 1000vh;
                    transition: 'opacity'.3s;
                }
        }
    }

    

    @media(max-width: 1024px) {
        margin-top: 24px;
    }

    @media(max-width: 600px) {
        margin-top: 34px;
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__weight {
        font-weight: 400;
        font-size: 16px;
        line-height: 23px;
        color: $gray-2;
    }

    &__title {
        @media(max-width: 1023px) {
            font-size: 36px;
            line-height: 36px;
        }

        @media(max-width: 600px) {
            font-size: 25px;
            line-height: 33px;
            letter-spacing: 1px;
        }
    }

    &__pagination {
        display: none;
        margin: 0 35px;

        @media(max-width: 1023px) {
            display: flex;
        }
    }

    &__decor {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: -1;

        @media(max-width: 1023px) {
            display: none;
        }
    }

    &__container {
        position: relative;
    }

    &__price {
        font-weight: 900;
        font-size: 26px;
        color: $orange;

        @media(max-width: 1023px) {
            font-size: 23px;
        }
    }

    &__info {
        display: flex;
        align-items: center;
        margin-top: 18px;
        justify-content: space-between;

        @media(max-width: 1023px) {
            margin-top: 19px;
        }

        @media(max-width: 600px) {
            margin-top: 8px;
        }
    }

    &__inner {
        position: relative;
        display: flex;
        padding: 0 150px;

        @media(max-width: 1600px) {
            padding: 0 216px;
        }

        @media(max-width: 1335px) {
            padding: 0px 190px;
        }

        @media(max-width: 1300px) {
            display: flex;
            flex-direction: column;
            padding: 0;
        }

        &:after,
        &:before {
            display: inline-block;
            position: absolute;
            content: none;
            width: 100%;
            height: 52px;
            background-repeat: no-repeat;

            @media(max-width: 1023px) {
                content: '';
            }
        }

        &:after {
            background-image: url('../img/decor/d3.svg');
            top: 112px;
            right: -142px;

            @media(max-width: 1023px) {
                top: 123px;
                right: -291px;
                height: 95px;
            }

            @media(max-width: 767px) {
                right: -205px;
            }

            @media(max-width: 600px) {
                top: 122px;
                right: -72px;
                height: 95px;
                z-index: -1;
                width: 76%;
                background-size: contain;
            }
        }

        &:before {
            // background-image: url('../img/decor/d3.png');
            // top: 150px;
            // right: -142px;
            // background-color: red;

            // @media(max-width: 1023px) {
            //     top: 123px;
            //     right: -291px;
            //     height: 95px;
            // }
        }


    }

    &__left {
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        left: 0;
        height: 100%;
        padding-bottom: 63px;

        @media(max-width: 1300px) {
            position: relative;
            padding-bottom: 0;
        }
    }

    &__right {
        position: relative;

        @media(max-width: 1300px) {
            max-width: 840px;
            width: 100%;
            margin: 0 auto;
        }

        @media(max-width: 1235px) {
            margin-top: -430px;
            max-width: 700px;
        }

        @media(max-width: 1023px) {
            margin: 0;
            max-width: 100%;
            margin-top: 24px;
        }

        &:after {
            @media(max-width: 1023px) {
                position: absolute;
                content: "";
                height: 177px;
                background-image: url(../img/decor/d4.svg);
                top: 179px;
                right: -298px;
                width: 100%;
                z-index: -1;
            }

            @media(max-width: 767px) {
                right: -129px;
                height: 100px;
                background-repeat: no-repeat;
            }
        }
    }

    &__bloc {
        &--top {
            max-width: 355px;

            .dish__text {
                margin-top: 9px;

                @media(max-width: 1024px) {
                    font-size: 16px;
                    line-height: 21px;
                    margin-top: 13px;
                }

                @media(max-width: 600px) {
                    font-size: 14px;
                    line-height: 20px;
                    margin-top: 5px;
                }
            }
        }

        &--bottom {
            max-width: 260px;

            @media(max-width: 1235px) {
                margin-top: 315px;
            }

            @media(max-width: 1023px) {
                display: none;
            }
        }
    }

    &__cover {
        position: relative;
        padding: 0 170px;

        @media(max-width: 1600px) {
            padding: 0 100px 0 100px;
        }

        @media(max-width: 1335px) {
            padding: 0 90px 0 90px;
        }

        @media(max-width: 1235px) {
            padding: 0 60px 0 60px;
        }

        @media(max-width: 1023px) {
            padding: 0;
        }
    }

    &__image {
        width: 625px;
        height: 625px;
        margin: 0 auto;
        margin-top: -43px;

        @media(max-width: 1600px) {
            width: 580px;
            height: 580px;
        }

        @media(max-width: 1335px) {
            width: 560px;
            height: 560px;
        }

        @media(max-width: 1235px) {
            width: 485px;
            height: 485px;
        }

        @media(max-width: 1023px) {
            margin: 0;
            min-width: 480px;
            margin-left: -66px;
            margin-right: 11px;
        }

        @media(max-width: 767px) {
            margin-left: -150px;
        }

        @media(max-width: 640px) {
            width: 360px;
            height: 360px;
            min-width: initial;
            margin: 0 auto;
        }

        @media(max-width: 600px) {
            margin-left: -61px;
            width: 300px;
            height: 300px;
            min-width: initial;
        }

        img {
            height: 100%;
            width: 100%;
            object-fit: contain;
        }
    }

    &__box {
        display: flex;
        align-items: flex-end;
        max-width: 625px;

        &:after {
            position: absolute;
            content: "";
            width: calc(100% + 1300px);
            height: 0;
            background: rgba(0,0,0,.5);
            top: -1000%;
            bottom: 0;
            left: -103%;
            right: 0;
            opacity: 0;
            z-index: -1;
            transition: 'opacity'.3s;
        }

        @media(max-width: 1023px) {
            max-width: 100%;
        }

        @media(max-width: 640px) {
            flex-direction: column;
        }

       
    }

    &__article {
        font-weight: 900;
        font-size: 25px;
        line-height: 146%;

        // &:hover {
        //     color: $orange;
        // }

        @media(max-width: 1024px) {
            font-size: 20px;
            line-height: 20px;
        }

        @media(max-width: 600px) {
            letter-spacing: 0.4px;
        }
    }

    &__note {
        position: absolute;
        max-width: 315px;
        padding-bottom: 55px;
        right: -300px;

        @media(max-width: 1300px) {
            right: -272px;
        }

        @media(max-width: 1235px) {
            right: -180px;
            max-width: 250px;
            padding-bottom: 0px;
        }

        @media(max-width: 1023px) {
            position: relative;
            right: auto;
            max-width: 100%;
            padding-bottom: 23px;
        }

        @media(max-width: 600px) {
            padding-bottom: 0;
            margin-top: 5px;
        }

        .dish__text {
            margin-top: 8px;

            @media(max-width: 1023px) {
                margin-top: 11px;
            }
        }
    }

    &__link {
        position: relative;
        margin-top: 26px;
        display: inline-block;
        color: $orange;
        padding-bottom: 5px;
        transition: .4s ease;

        &:after {
            position: absolute;
            content: "";
            width: 100%;
            height: 1px;
            background-color: orange;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            transition: .4s ease;
        }

        &:hover {
            color: $black;

            &:after {
                background-color: $black;
            }
        }
    }

    &__arrow {
        position: absolute;
        display: flex;
        width: 100%;
        justify-content: space-between;
        left: 0;
        top: -46px;
        bottom: 0;
        margin: auto;
        height: 100%;
        align-items: center;

        @media(max-width: 1023px) {
            position: relative;
            align-items: center;
            justify-content: center;
            margin-top: 15px;
            top: 0;
        }

        @media(max-width: 600px) {
            margin-top: 30px;
        }

        .dish__arr--next.swiper-button-disabled,
        .dish__arr--prev.swiper-button-disabled {
            opacity: 0;
        }
    }



    &__el {
        position: absolute;

        background-size: contain;
        background-repeat: no-repeat;

        &:first-child {
            left: 105px;
            top: 37px;
            width: 418px;
            height: 340px;
        }

        &:last-child {
            left: auto;
            top: -7px;
            width: 763px;
            height: 605px;
            right: -185px;
        }
    }

    .swiper-wrapper {
        max-width: 625px;

        @media(max-width: 1023px) {
            max-width: 100%;
        }
    }
}