.offer {
    margin-top: 85px;

    @media(max-width: 1024px) {
        margin-top: 52px;
    }

    @media(max-width: 600px) {
        margin-top: 39px;
    }

    &__txt {
        font-weight: 500;

        @media(max-width: 600px) {
            margin-top: 14px;
        }
    }

    &__pagination {
        margin-top: -25px;
        z-index: 1;
        position: relative;
        display: flex;
        justify-content: center;

        .swiper-pagination-bullet {
            background-color: $gray;
            opacity: .5;
        }

        .swiper-pagination-bullet-active {
            opacity: 1;
        }
    }

    &__grid {
        display: flex;
        justify-content: space-between;
        flex-flow: column wrap;
        max-height: 794px;

        @media(max-width: 600px) {
            justify-content: initial;
            flex-flow: initial;
            max-height: initial;
            height: 100%;
        }

        .btn {
            align-self: center;
            margin-top: 50px;
            margin-left: 40px;

            @media(max-width: 1024px) {
                margin-top: 31px;
                margin-left: 30px;
            }
        }
    }

    &__wrapper {
        max-width: 565px;
    }

    &__article {
        line-height: 43px;
        margin-bottom: 15px;
        max-width: 415px;
        text-transform: initial;
        font-weight: 700;

        @media(max-width: 1024px) {
            font-size: 28px;
            line-height: 28px;
            margin-bottom: 9px;
            letter-spacing: 0px;
        }

        @media(max-width: 600px) {
            letter-spacing: 1.3px;
            max-width: 330px;
            margin: 0 auto;
            font-size: 25px;
            line-height: 27px;
        }

    }

    &__img {
        position: absolute;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        z-index: -1;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: .6s ease;
        }
    }

    &__item {
        position: relative;
        display: flex;
        align-items: flex-end;
        // max-width: 522px;
        width: calc(42.576% - 40px);
        padding: 60px 34px 30px 30px;
        color: white;
        background-size: cover;
        border-radius: 4px;
        z-index: 1;
        margin-left: 40px;
        transition: 1s ease;
        background-size: 100% 100%;

        @media(max-width: 1024px) {
            background-size: cover;
        }

        &:hover img {
            transform: scale(1.1);
        }

        &:after {
            // position: absolute;
            // content: "";
            // background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.71) 100%);
            // opacity: .75;
            // top: 0;
            // bottom: 0;
            // left: 0;
            // right: 0;
            // margin: auto;
        }

        @media(max-width: 1024px) {
            margin-left: 30px;
            width: calc(50% - 30px);
            padding: 55px 25px 22px;
        }

        @media(max-width: 600px) {
            margin-left: initial;
            min-height: 100% !important;
            align-items: normal;
            text-align: center;
            padding: 72px 11px 22px;
        }

        // @media(max-width: 600px) {
        //     margin-left: initial;
        //     width: auto;
        // }

        &:after {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%);
            opacity: .4;
            border-radius: 4px;
            z-index: -1;
            transition: .4s ease;
        }

        &:hover:after {
            opacity: .75;
            // background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.71) 100%);
        }

        &:nth-child(2) {
            margin-top: 40px;

            @media(max-width: 1024px) {
                margin-top: 30px;
            }

            @media(max-width: 600px) {
                margin-top: 0;
            }
        }

        &:not(:nth-child(3)) {
            // margin-right: 40px;

            @media(max-width: 1024px) {
                //     margin-right: 30px;
            }
        }

        &--small {
            min-height: 237px;

            @media(max-width: 1024px) {
                min-height: initial;
            }
        }

        &--middle {
            min-height: 389px;

            @media(max-width: 1024px) {
                min-height: 262px;
            }
        }

        &--big {
            min-height: 556px;
            width: calc(57.2% - 40px);
            // max-width: 50%;

            @media(max-width: 1024px) {
                margin-left: 30px;
                width: calc(50% - 30px);
                min-height: 402px;
            }

            @media(max-width: 600px) {
                min-height: initial;
                margin-left: initial;
            }

        }
    }

    &__slider {
        margin-top: 35px;
        margin-left: -40px;


        @media(max-width: 1024px) {
            margin-top: 28px;
            margin-left: -30px;
        }

        @media(max-width: 600px) {
            margin-left: initial;
            height: 300px;
            margin-top: 22px;
            overflow: hidden;
        }
    }

    #offer__all {
        @media(max-width: 600px) {
            margin-top: 18px;
        }

        .btn {
            @media(max-width: 600px) {
                max-width: 100%;
                width: 100%;
                text-align: center;
            }
        }
    }
}